/**
 * 
 * @param {Number} x  x
 * @param {Number} y  y
 * @returns {Number} 
 * @author chensuiyi <chensuiyi.com>
 */
export function math_Minus(x, y) {
    return x - y;
}

/**
 * 
 * @param {Number} x  x
 * @param {Number} y  y
 * @returns {Number} 
 * @author chensuiyi <chensuiyi.com>
 */
export function math_Plus(x, y) {
    return x + y;
}
